function App() {
  // console.log(deployments)
  // // Read more about useDapp on https://usedapp.io/
  // var scytheLock = deployments[window.useChain][0].contracts.ScytheEnter;
  // const { error: contractCallError, value: tokenBalance } =
  //   useCall({
  //      contract: new Contract(scytheLock.address,scytheLock.abi),
  //      method: "estETHFromShare",
  //     args : [ethers.utils.parseEther('0.1')]
  //   }) ?? {};
    
  //   useEffect(() => {
  //     if(contractCallError) {
  //       console.error("Error while calling contract:", contractCallError.message);
  //       return;
  //     }
  //     if(tokenBalance){
  //       console.log("PPFS:", tokenBalance[0],ethers.utils.formatEther(tokenBalance[0]));
  //     }
  //     console.log(deployments)
  //    // console.log(ethers.utils.formatEther(tokenBalance[0]));
  //   },[tokenBalance,contractCallError]);
  // useEffect(() => {
  //   console.log(window.provider);
  // },[]);

  // const ref = window.ref = createRef();

  // const { account } = useEthers()
  // const etherBalance = useEtherBalance(account)

  const { error } = useEthers()
  const href = useMatch('/');
  const terms = useMatch('/terms');
  const isssoon = useMatch('/soon');


  useEffect(() => {
    if (error) {
      console.log(error)
    }
  }, [error])

  const [open,setOpen] = useState(false);
  const [modalData,setModalData] = useState({
    title: '--',
    subtitle: '---',
    body: '---',
    icon:'loader'
  });
  const [modalPart,setModalPart] = useState(null);
  window.changeAlert = function(isOpen,data){
    setOpen(isOpen);
    setModalData(data);
    setModalPart(null);
  }
  window.changeModalPart = function(part){
    setModalPart(part);
  }
  

  return (
    <>
      {isssoon?(<></>):<Header />}
      <MainStyle size={(href||terms)?"content":"tight"} style={{marginTop:'81px'}}>
          {/* Ether balance:
          <p className="bold">{ethers.utils.formatEther(etherBalance[0])} ETH</p> */}
        {/* <BalanceViewer settings={stakingOps['usd']} /> */}
        
        <Routes>
          <Route path="/soon" element={<Soon/>} />
          <Route path="/" element={<Landing settings={stakingOps}/>} />
          <Route path="/terms" element={<Terms/>} />
          <Route path="/ftm" element={<Staking name={'usdstaking'} settings={stakingOps['ftm']}/>} />
          <Route path="/usd" element={<Staking key={'usdstaking'} settings={stakingOps['usd']}/>} />
        </Routes>

        <Modal
          center
          open={open}
          onClose={modalData.icon==='loader'?(null):(function(){setOpen(false)})}
          subtitle={modalData.subtitle}
          title={modalPart?(modalData.title+' '+modalPart):(modalData.title)}
          titleIcon={modalData.icon==='loader'?(<Loader size="large" />):((modalData.icon==='success')?(<Success color="green" height={64} width={64}/>):(<Error color="red" height={64} width={64}/>))}
        >
          <br />
          <Text
            color="secondary"
            size="xxs"
          >
            {modalData.body}
          </Text>
        </Modal>
          
      </MainStyle>
      {isssoon?(<></>):<Footer />}
    </>
  );
}

export default App;
