
import { createRef, useEffect, useState } from 'react';
import { H2,Text,Block,InputGroup,SelectIcon,Option,Input,Button,DataTable,DataTableRow,Accordion } from '@lidofinance/lido-ui';
import StakeIcon from './stakeIcons';
import { useEthers,useCall, useTokenAllowance,useEtherBalance,useTokenBalance } from "@usedapp/core";
import { Contract } from "@ethersproject/contracts";
import { ethers } from "ethers";
import {SliderContainerStyle, SliderHolderStyle, SliderOptionStyle,SliderStyle} from "./stakingStyles.js";
import BalanceViewer from "./balances";
import { formatEther, parseEther } from '@ethersproject/units'
import { create } from '@mui/material/styles/createTransitions';

