const NATIVE = 'WDOGE'
const WETH = '0xB7ddC6414bf4F5515b52D8BdD69973Ae205ff101'
function feetopercent(fee){
    if(!fee){
        return 'error';
    }
    return (fee.toNumber()/100).toFixed(2)
}
var truncateDecimals = function (number, digits) {
    var multiplier = Math.pow(10, digits),
        adjustedNum = number * multiplier,
        truncatedNum = Math[adjustedNum < 0 ? 'ceil' : 'floor'](adjustedNum);

    return truncatedNum / multiplier;
}
function fixNumber(num,decimals){
    if(!num.includes('.')){
        return num;
    }
    var parts = num.toString().split('.');
    parts[1] = parts[1].substr(0,decimals)
    return parts.join('.')
}

const Staking = (props) => {

    const config = props.settings;

    const { account, activateBrowserWallet,library, deactivate, error } = useEthers();

    const [currency,setCurrency] = useState(props.settings.depositCurrencies[0]);
    const [amountRaw,setAmount] = useState('');
    const [fee,setFee] = useState(0);
    const [lpOut,setLpOut] = useState(0);
    const [isStaking,setStaking] = useState(true);

    const iu = !!config.LP;
    var doubleLiq = iu&&!isStaking&&currency.includes('+')

    const isWant = iu&&config.vaultAddress==config.currencies[currency];
    
    var amount = (amountRaw==='0.0'||amountRaw==='')? ((isWant&&iu)?'0.000000000000001':'0.0001'):amountRaw;

    const toosmall = amount=='0.0001'||parseFloat(amount)<=0.000000000000001;

    const etherBalance = useEtherBalance(account);

    const { error: chefInfoError, value: chefInfo } =
        useCall({
            contract: new Contract(config.chef.address,config.chef.abi),
            method: "userInfo",
            args : [config.chefIndex,account]
        }) ?? {};

    const shareBalance = chefInfo?chefInfo[0]:0;
    
    var currencyBalance = useTokenBalance(isStaking?(currency==NATIVE?WETH:config.currencies[currency]||WETH):(window.scytheLock?window.scytheLock.address:WETH), account);
    if(!isStaking){
        currencyBalance = shareBalance;
    }
    
    var maxAmmt = 0;
    if(etherBalance&&currencyBalance){
        if(!isStaking){
            maxAmmt = formatEther(currencyBalance.mul(config.sharemult))-0;
        }else if(currency==NATIVE){
            maxAmmt = formatEther(etherBalance)-0;
        }else {
            maxAmmt = ethers.utils.formatUnits(currencyBalance,config.tokenDecimals[currency]||18)-0;
        }
    }

    const { error: feeError, value: fees } =
        useCall({
            contract: new Contract(config.contract.address,config.contract.abi),
            method: isStaking?"buyback":"reflection",
        }) ?? {};
    const { error: pplsError, value: pricePerLockShare } =
        useCall({
            contract: new Contract(config.contract.address,config.contract.abi),
            method: "getPricePerFullShare",
        }) ?? {};
    

    const outEst = 1;
    const swapEst = 1;

    if(pricePerLockShare){
        console.log(ethers.utils.formatEther(pricePerLockShare[0]))
    }

    useEffect(function(){
        (async function(){
            
            var zapper = new ethers.Contract(config.ZapperHH.address,config.ZapperHH.abi,window.signer);
            // zapper.on("Transfer", (from, to, value, event) => {
            //     console.log({
            //         from: from,
            //         to: to,
            //         value: value.toString(),
            //         data: event
            //     });
            // });
            console.log('CS RESULT: ')
            var csresult = await zapper.callStatic.zapInLP(WETH,config.vault,parseEther('1.0'),0,config.contract.address,0,{
                value:parseEther('1.0')
            });
            console.log('CS RESULT: /')
            console.log(csresult);


        })();
    },[])
    
    const useMin = false;


    console.log(amount,maxAmmt)
    const hasEnough = (etherBalance&&currencyBalance&&!toosmall)?(maxAmmt>0&&maxAmmt>=amount):true;

    function changeStaking(){
        console.log('changeStaking')
        setCurrency(!isStaking?config.depositCurrencies[0]:config.withdrawCurrencies[0]);
        setStaking(!isStaking)   
        setAmount('')
    }
    async function useMax(){
        if(!account){
            return;
        }
        if(isStaking){
            if(currency=='WDOGE'){
                setAmount(formatEther((await window.provider.getBalance(account)).sub(ethers.utils.parseEther('2'))))
            }else{
                const contract = new ethers.Contract(config.currencies[currency],config.contract.abi,window.signer);
                var max = ethers.utils.formatUnits((await contract.balanceOf(account)),config.tokenDecimals[currency]||18);
                setAmount(max);
            }
        }else{
            setAmount(formatEther((shareBalance).mul(config.sharemult)));
        }
    }

    const ref = createRef();

    async function stakeTokens(){
        console.log('staking ',currency,amount)
        if(!account){
            activateBrowserWallet();
            return;
        }
        try{
            window.signer = library.getSigner();
            if(isStaking){
                window.changeAlert(true,{
                    title: 'Depositing '+currency,
                    subtitle: 'You are currently depositing '+amount+' '+currency,
                    body: 'Please approve the transactions in your metamask wallet as they come up',
                    icon:'loader'
                })
                await config.deposit(currency,amount,window.enter,window.scytheLock,account,useMin);
                window.changeAlert(true,{
                    title: 'Deposited '+currency,
                    subtitle: 'You have deposited '+amount+' '+currency,
                    body: '',
                    icon:'success'
                })
            }else{
                window.changeAlert(true,{
                    title: 'Withdrawing to '+currency,
                    subtitle: 'You are currently withdrawing '+amount+' Shares',
                    body: 'Please approve the transactions in your metamask wallet as they come up',
                    icon:'loader'
                })
                await config.withdraw(currency,amount,window.enter,window.scytheLock,account,useMin);
                window.changeAlert(true,{
                    title: 'Withdrew to '+currency,
                    subtitle: 'You have withdrawn '+amount+' Shares',
                    body: '',
                    icon:'success'
                })
            }
        } catch(e){
            if(e.code===4001&&window.changeAlert){
                window.changeAlert(true,{
                    title: 'Error',
                    subtitle: 'User denied transaction',
                    body: 'Please try again',
                    icon:'error'
                  })
            }
            console.log('error',e)
        }
    }
    var stats = [
        {
            title: isStaking?'$HLH Buyback Fee':'Reflection',
            value: (fees?feetopercent(fees[0]):'err')+'%',
        },
    ]
    

    const migrating = false;//config.name=='usd'&&isStaking;

    return (
      <>
        <div style={{textAlign:'center'}}>
            <H2 style={{marginBottom:'14px'}} color="text">{isStaking?"Deposit":"Withdraw"} {config.symbol}</H2>
            <h4 style={{marginBottom:'16px',color:"rgba(255, 255, 255, 0.8)",fontWeight:"500"}}>{config.desc}</h4>
            <SliderContainerStyle>
                <SliderHolderStyle onClick={changeStaking}>
                    <SliderStyle style={{"left":isStaking?'2px':'calc(100% - 152px)'}}></SliderStyle>
                    <SliderOptionStyle key='depositSlider'>
                        Deposit
                    </SliderOptionStyle>
                    <SliderOptionStyle key='withdrawSlider'>
                        Withdraw
                    </SliderOptionStyle>
                </SliderHolderStyle>
            </SliderContainerStyle>
            <Block  style={{padding:'0px 0px 0px 0px'}}>
                <BalanceViewer settings={config} />
            <Block color="accent" >
            
                <InputGroup fullwidth ref={ref} style={{marginBottom:'16px'}}>
                    <SelectIcon fullwidth
                        anchorRef={ref}
                        icon={<StakeIcon coin={currency} />}
                        onChange={function noRefCheck(event){
                            setAmount('');
                            setCurrency(event);
                            
                            // console.log(event)
                        }}
                        value={currency}
                    >
                        {
                            (isStaking?props.settings.depositCurrencies:props.settings.withdrawCurrencies).map((currency,index) => {
                                return <Option
                                        leftDecorator={<StakeIcon coin={currency} />}
                                        key={currency+'select'}
                                        value={currency}
                                        
                                        >
                                        {currency}
                                        </Option>
                            })
                        }
                    </SelectIcon>
                    <Input fullwidth
                        placeholder="Amount"
                        label={isStaking?"Amount":"Shares"}
                        rightDecorator={
                            <Button color="primary" onClick={useMax} size="xs" variant="translucent">
                                {(isStaking&&currency=='WDOGE')?'Max-2':'Max'}
                            </Button>
                        }
                        value={amountRaw}
                        onChange={(event)=>{setAmount(event.target.value)}}
                    />
                </InputGroup>
                
                <Button disabled={(migrating)||(((!amount||toosmall)&&account)||!hasEnough)} style={{marginBottom:'16px'}} color={hasEnough?(migrating?"warning":"primary"):"error"} fullwidth size="md" variant="filled" onClick={stakeTokens}>
                    {(migrating)?('Migrating'):(account?((hasEnough)?(isStaking?"Deposit":"Withdraw"):'Insufficient Balance'):'Connect Wallet')}
                </Button>
                <div style={{marginBottom:'16px',textAlign:'left'}}>
                    <DataTable style={{display:''}}>
                        {stats.map(function(stat){
                            return (
                                <DataTableRow key={stat.title+'stat'} loading={!fees} title={stat.title} help={stat.help}>
                                    {/* !fees||!(outEst&&(swapEst||doubleLiq||isWant)&&amount) */}
                                    <Text color={stat.strong} size="xs" strong={!!stat.strong}>{stat.value}</Text>
                                </DataTableRow>
                            )
                        })}
                    </DataTable>
                </div>
                </Block>
            </Block>
            
        </div>
        {
            config.info.map(function(info){
                return (
                    <Accordion
                        style={{marginTop:'16px'}}
                        summary={info.summary}
                        key = {info.summary}
                    >
                        {info.details}
                    </Accordion> 
                )
            })
        }
      </>
    );
  }
  
  export default Staking;