function calcAPY(p1,p2,d1,d2){
    var d2 = d2||(new Date()-0)
    var year = 1000*60*60*24*365
    var ratio = p2/p1;
    
    

    return Math.pow(ratio,year/(d2-d1))
}
function dAPY(apy){
    return ((apy-1)*100).toFixed(2)+'%'
}

