    const TVL = (totalScythe&&per)?(totalScythe[0].mul(per).div(ethers.utils.parseEther('1.0'))):(0)
    const TVE = (HLHPrice&&emissions)?(HLHPrice.mul(emissions[0].mul(60*60*24)).div(ethers.utils.parseEther('1.0'))):0


    if(TVL){
        console.log('TVL: ',ethers.utils.formatUnits(TVL,config.tokenDecimals[config.LPcoins[0]]||18));
    }




